nav {
  height: 95px;
  background-color: transparent;

  display: flex;
  align-items: center;

  position: absolute;
  z-index: 1;

  width: 100%;
}

.logo--image--container {
  margin: 1em 0em 0 1.75em;
}

.logo--image {
  height: 5em;
  width: 100%;
}

.links--container {
  width: 25%;

  display: flex;
  justify-content: space-between;

  margin: 0 2em 0 auto;
}

.links {
  text-decoration: none;
  color: black;

  font-weight: 300;
  font-size: 1.03em;
  letter-spacing: 0.03em;
}
