.imageWithText {
  background-color: white;

  height: 80vh;
  width: 70%;

  margin: 0 auto 0 auto;

  display: flex;
  align-items: center;
  justify-content: center;
}

.imageWithText--image {
  height: 80%;

  margin-left: auto;
}

.imageWithText--text {
  margin: 1em 1em 1em 1em;
  font-size: 4em;
  font-weight: 400;
  line-height: 1.05em;

  /* text-align: r; */

  white-space: pre;
}
